
import { ref, defineComponent, reactive, unref } from 'vue'
import { useRouter } from 'vue-router'
import '../mock/login.js'
import { login } from '../axios'

// 对密码和邮箱进行类型限制
interface loginData {
  email: string;
  pass: string;
}

export default defineComponent({
  setup () {
    const loginForm = ref<loginData>({
      email: '',
      pass: ''
    })
    const router = useRouter()
    const loginFormRef = ref()
    // 自定义验证规则
    const validatePass = (rule, value, callback) => {
      //  密码只能由大小写英文字母或数字开头，且由大小写英文字母_.组成
      const reg = /^[A-Za-z0-9][A-Za-z0-9_.]{5,14}$/
      if (!value.match(reg)) {
        callback(new Error('密码由字母或数字开头，且只能为字母,数字,下划线及（.）'))
      } else {
        callback()
      }
    }
    // 定义校验规则
    const loginRules = reactive({
      email: [
        { required: true, message: '邮箱不能为空', trigger: 'blur' },
        { type: 'email', message: '邮箱格式不正确', trigger: 'blur' }
      ],
      pass: [
        { required: true, message: '密码不能为空', trigger: 'blur' },
        { min: 6, max: 15, message: '密码位数只能在6~15之间', trigger: 'blur' },
        { validator: validatePass, trigger: 'blur' }
      ]
    })
    // 是否登录成功
    const successMode = ref<0|1|2>(0)
    // 失败提示
    const tip = ref<string>('')
    // 获取失败提示
    const getTip = (message) => {
      tip.value = message
    }
    // 重置表单
    const resetForm = () => {
      // 笨办法这么写：
      // loginForm.value.email = ''
      // loginForm.value.pass = ''
      // 明眼人这么写：
      const form = unref(loginFormRef)
      form.resetFields()
    }
    // 路由跳转
    const goto = () => {
      router.push('/')
    }
    // 改变登录状态
    const changeMode = (mode) => {
      successMode.value = mode
    }
    // 表单提交
    const submitForm = async () => {
      const form = unref(loginFormRef)
      if (!form) {
        return
      }
      try {
        await form.validate()
        const { email, pass } = loginForm.value
        login(email, pass, goto, changeMode, getTip)
      } catch (err) {
        console.log(err)
      }
    }
    return {
      loginFormRef,
      loginForm,
      loginRules,
      resetForm,
      submitForm,
      successMode,
      goto,
      tip
      // setCookies
    }
  }
})
